/** @format */

.Header {
  padding: 21px 10px 20px 10px;
  display: grid;
  grid-template-columns: 10% 60% 30%;
  background-color: #24272c;
}

a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.Header div {
  margin: auto;
}

.Header_Text {
  margin-left: 3.4% !important;
  text-align: start;
}

.Header_Text .Title h1 {
  font-family: Roboto, "sans-serif";
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
}

.Header_Text .Sub p {
  opacity: 0.7;
  font-family: Roboto, "sans-serif";
  font-size: 12px;
  color: #ffffff;
}

.Next button {
  font-family: Roboto;
  padding: 9px 17px 9px 17px;
  border-radius: 4px;
  background-color: #12b0ee;
}

.Next button svg {
  align-self: center;
  margin-left: 10px;
  transform: rotateY(180deg);
}

.LeftArrow {
  align-self: center;
  margin: auto;
}

.Next button:disabled {
  background-color: #373b43;
  box-shadow: none;
  opacity: 0.3;
}
