/** @format */

.Create_Template {
  background-color: #24272c;
  height: 100%;
  font-family: Roboto;
}

.SVG_Container {
  width: 95%;
  height: 50%;
  margin: auto;
  background-color: #34373b;
  border: 2px dashed #4a4d51;
  border-radius: 2px;
  display: flex;
  position: relative;
}

.SVG_Container input {
  background-color: transparent;
  border-radius: 5px;
  margin: 0;
  width: 100%;
  position: absolute;
  height: 100%;
  padding: 0;
}

.SVG_Container form {
  position: absolute;
  width: 100%;
  height: 100%;
}

.Upload_Handler {
  position: relative;
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  border-radius: 4px;
}

.Upload_Handler h3 {
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  color: #12b0ee;
  align-self: center;
  margin: auto;
}

.SVG_Container input::-webkit-file-upload-button {
  width: 100%;
  height: 100%;
  opacity: 0;
}

.Upload_Btn {
  margin: auto;
}

.Upload_Btn button {
  margin: auto;
  border-radius: 4px;
}

.Upload_Btn p {
  font-family: Roboto;
  font-size: 15px;
  opacity: 0.7;
  text-align: center;
  color: #ffffff;
  margin-bottom: 10px;
}

.BackDrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.Popup_Container {
  position: absolute;
  z-index: 500;
  background-color: white;
  width: 70%;
  height: auto;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 30%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
}

.Popup_Container label {
  position: unset;
}

form div {
  margin-top: 10px;
}

input {
  margin: 10px auto;
  padding-left: 15px;
  padding-top: 0;
  height: 40px;
  font-size: 14px;
}

.Color_Container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
